@media (min-width: 768px) {
  .block {
    display: flex;
  }
}

@media screen and (max-width: 480px) {
  .mobile {
    display: flex;
  }

  .header_main {
    height: 46.875rem;
  }

  .through-panel__col_left {
    width: 50%;
  }

  .through-panel__col_center {
    display: none;
  }

  .through-panel__col_right {
    width: 50%;
  }

  .button_black_mobile {
    display: flex;
    margin-top: 1.875rem;
  }

  .header-bottom__item {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
  }

  .header-bottom__item-title {
    font-size: 1.25rem;
  }

  .header-bottom__item-desc {
    font-size: 1rem;
  }

  .content-top {
    height: 21.875rem;
  }

  .content-slider {
    top: -15%;
  }

  .content-var__item {
    margin-right: 0.0625rem;
  }

  .content-var__point-title {
    font-size: 0.75rem;
  }

  .content-var__point-oldprice {
    font-size: 0.75rem;
  }

  .content-var__point-newprice {
    font-size: 0.875rem;
  }

  .content-inf__aboutcomp {
    flex-direction: column;
    align-items: center;
  }

  .content-inf__pic {
    margin-right: 0;
  }

  .content-inf__text {
    display: none;
  }

  .content-inf__desc {
    display: none;
  }

  .content-inf__title {
    display: none;
  }

  .content-inf__sometext {
    margin-top: 5rem;
    flex-direction: column;
    align-items: center;
  }

  .content-inf__sometext .content-inf__imgblock-title {
    left: -120%;
  }

  .content-inf__sometext .content-inf__imgblock-link {
    display: none;
  }

  .content-inf__imgblock {
    margin-left: 0;
    width: 100%;
  }

  .content-inf__imgblock-title {
    left: -155%;
  }

  .content-inf__imgblock-link {
    display: none;
  }

  .footer__col {
    display: none;
  }

  .footer__col-mobile {
    display: flex;
  }

  .header-content__col_left {
    width: 53%;
  }

  .header-content__col-container {
    margin-right: 0.9375rem;
    margin-left: 0.9375rem;
  }

  .header-content__top {
    display: none;
  }

  .header-content__list {
    flex-direction: column;
  }

  .header-content__title {
    font-size: 1.875rem;
    margin-top: 2.5rem;
  }

  .header-content__oldprice {
    font-size: 0.875rem;
    margin-top: 0.9375rem;
  }

  .header-content__newprice {
    font-size: 1.5rem;
    margin-top: 2.5rem;
  }

  .header-content__point {
    font-size: 0.875rem;
  }

  .header-content__inf {
    font-size: 0.875rem;
  }

  .header-content__avail {
    font-size: 0.875rem;
    margin-top: 2.1875rem;
  }

  .bx-wrapper .bx-prev {
    left: -18%;
  }
}